/*-------------------------------------------------------------------------------
  Nav Bar
-------------------------------------------------------------------------------*/
/*General*/
.navbar {
  width: 100%;
  height: 7vh;
  position: fixed;
  background-color: #41414d;

  display: flex;
  justify-content: space-between;
}

.navbar .navbar-hidden{
  display: none;
}


/*Enter button*/
.navbar .button {
  height: 7.05vh;
  width: calc(7vh * 1.8);

  margin-top: 0px;

  background-color: #f0f0f5;
  color: grey;

  border-radius: 0px;
  font-size: 2vh;

  display: flex;
  align-items: center;
  justify-content: space-around;
}

/*Hidden menu*/
.navbar .navbar-menu .button-wider{
  width: calc(7vh * 2.5);
}

.navbar .navbar-menu .nav-list-hidden{
  display: none;
}

.navbar .navbar-menu div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(7vh * 2.5);
  height: 100vh;
  background: #41414d;
  color: grey;
}

.navbar .navbar-menu div .navbar-button{
  display: flex;
  align-items: center;
  justify-content: center;

  height: 5vh;
  width: calc(6vh * 2.5);

  background: #f0f0f5;
  border-radius: 5px;
  color: grey;
  font-weight: 700;
  margin-top: 10px;
  text-decoration: none;
  font-size: 1.5vh;
}

.navbar .navbar-menu div .navbar-button:hover{
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.navbar .navbar-menu div .dif-color{
  border-color: red;
}

/*Logo*/
.navbar div {
  height: 7vh;
}

.navbar img {
  height: 7vh;
}

.navbar img:hover{
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

/*Profile*/
.navbar .navbar-profile .navbar-button{
  height: 7.05vh;
  width: calc(7vh * 1.8);

  margin-top: 0px;

  background-color: #f0f0f5;
  color: grey;

  border-radius: 0px;
  font-size: 2vh;

  display: flex;
  align-items: center;
  justify-content: space-around;
  text-decoration: none;
}

.navbar .navbar-button:hover{
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}
